// @flow
/* eslint-disable react/prop-types */

import React from "react";
import classNames from "classnames";
import Select from "react-select";
import { withStyles } from "@material-ui/core/es/styles";
import Typography from "@material-ui/core/es/Typography";
import TextField from "@material-ui/core/es/TextField";
import Paper from "@material-ui/core/es/Paper";
import Chip from "@material-ui/core/es/Chip";
import MenuItem from "@material-ui/core/es/MenuItem";
import { emphasize } from "@material-ui/core/es/styles/colorManipulator";

const styles = theme => ({
  root: {
    flexGrow: 1,
    height: 35,
    width: "23em",
    maxWidth: "calc(100% - 1.2rem)",
    marginLeft: "0.6rem"
  },
  input: {
    display: "flex",
    padding: 0
  },
  valueContainer: {
    display: "flex",
    flexWrap: "wrap",
    flex: 1,
    alignItems: "center"
  },
  chip: {
    margin: `${theme.spacing.unit / 2}px ${theme.spacing.unit / 4}px`
  },
  chipFocused: {
    backgroundColor: emphasize(
      theme.palette.type === "light"
        ? theme.palette.grey[300]
        : theme.palette.grey[700],
      0.08
    )
  },
  noOptionsMessage: {
    padding: `${theme.spacing.unit}px ${theme.spacing.unit * 2}px`
  },
  singleValue: {
    fontSize: 30,
    margin: "0 auto 0 0.2em"
  },
  placeholder: {
    position: "absolute",
    left: 2,
    fontSize: 30
  },
  paper: {
    position: "absolute",
    zIndex: 100,
    marginTop: theme.spacing.unit,
    left: 0,
    right: 0
  },
  divider: {
    height: theme.spacing.unit * 2
  }
});

function NoOptionsMessage(props) {
  return (
    <Typography
      color="textSecondary"
      className={props.selectProps.classes.noOptionsMessage}
      {...props.innerProps}
    >
      {props.children}
    </Typography>
  );
}

function inputComponent({ inputRef, ...props }) {
  return <div ref={inputRef} {...props} />;
}

function Control(props) {
  return (
    <TextField
      fullWidth
      InputProps={{
        inputComponent,
        inputProps: {
          className: props.selectProps.classes.input,
          inputRef: props.innerRef,
          children: props.children,
          ...props.innerProps
        }
      }}
      {...props.selectProps.textFieldProps}
    />
  );
}

function Option(props) {
  return (
    <MenuItem
      buttonRef={props.innerRef}
      selected={props.isFocused}
      component="div"
      style={{
        fontWeight: props.isSelected ? 500 : 400
      }}
      {...props.innerProps}
    >
      {props.children}
    </MenuItem>
  );
}

function Placeholder(props) {
  return (
    <Typography
      color="textSecondary"
      className={props.selectProps.classes.placeholder}
      {...props.innerProps}
    >
      {props.children}
    </Typography>
  );
}

function SingleValue(props) {
  return (
    <Typography
      className={props.selectProps.classes.singleValue}
      color="primary"
      {...props.innerProps}
    >
      {props.children}
    </Typography>
  );
}

function ValueContainer(props) {
  return (
    <div className={props.selectProps.classes.valueContainer}>
      {props.children}
    </div>
  );
}

function MultiValue(props) {
  return (
    <Chip
      tabIndex={-1}
      label={props.children}
      className={classNames(props.selectProps.classes.chip, {
        [props.selectProps.classes.chipFocused]: props.isFocused
      })}
      onDelete={event => {
        props.removeProps.onClick();
        props.removeProps.onMouseDown(event);
      }}
    />
  );
}

function Menu(props) {
  return (
    <Paper
      square
      className={props.selectProps.classes.paper}
      {...props.innerProps}
    >
      {props.children}
    </Paper>
  );
}

function IndicatorsContainer() {
  return <div />;
}

const components = {
  Option,
  Control,
  NoOptionsMessage,
  Placeholder,
  SingleValue,
  MultiValue,
  ValueContainer,
  Menu,
  IndicatorsContainer
};

class IntegrationReactSelect extends React.Component<any, any> {
  render() {
    const { classes, theme, values, selected, select } = this.props;

    const selectStyles = {
      input: base => ({
        ...base,
        color: theme.palette.text.primary
      })
    };

    return (
      <div className={classes.root} style={{ marginBottom: "2rem" }}>
        <Select
          classes={classes}
          styles={selectStyles}
          options={values}
          components={components}
          value={values.find(val => val.value === selected)}
          onChange={val => select(val.value)}
        />
      </div>
    );
  }
}

const CustomDistrictSelect = withStyles(styles, { withTheme: true })(
  IntegrationReactSelect
);

type Props = {
  selected: string,
  values: string[],
  select: (s: string) => void
};

export const DistrictSelect = ({ values, selected, select }: Props) => (
  <CustomDistrictSelect
    values={values.map(val => ({ value: val, label: val }))}
    selected={selected}
    select={select}
  />
);
