// @flow

import React, { Fragment } from "react";
import ReactDOM from "react-dom";
import { createGlobalStyle } from "styled-components";
import { MuiThemeProvider, createMuiTheme } from "@material-ui/core/es/styles";
import { hot } from "react-hot-loader";
import App from "./containers/App";
import cstm from "./fonts/CSTM-Xprmntl.woff2";
import bebasBold from "./fonts/bebasneue_bold.woff2";
import rubikBlack from "./fonts/rubik_black.woff2";

const theme = createMuiTheme({
  overrides: {
    MuiTab: {
      selected: {
        color: "black !important",
        zIndex: 10
      },
      root: {
        color: "#ffdb4d !important",
        minWidth: "48px !important",
        fontSize: "30px !important"
      },
      labelContainer: {
        padding: "6px 6px !important"
      }
    },
    MuiTabs: {
      indicator: {
        height: "48px",
        borderRadius: "5px"
      },
      root: {
        marginBottom: "0.3rem",
        marginLeft: "0.6rem"
      }
    },
    MuiButton: {
      label: {
        fontSize: "30px !important",
        padding: "0.1em 0.2em"
      },
      root: {
        padding: "0.1em 0.2em",
        marginLeft: "0.6rem"
      }
    },
    MuiTypography: {
      root: {
        margin: "0 1rem"
      }
    },
    MuiGrid: {
      container: {
        marginBottom: "6rem"
      }
    }
  },
  typography: {
    fontFamily: "BebasNue"
  },
  palette: {
    type: "dark",
    primary: {
      main: "#ffdb4d"
    },
    secondary: {
      main: "#fff"
    }
  }
});

const GlobalStyle = createGlobalStyle`
  body, html {
    margin: 0;
    min-height: 100vh;
    background: #1b1b1b;
  }

  @font-face {
    font-family: 'CSTMXPRMNTL0101';
    src: url(${cstm}) format('woff2');
    font-weight: normal;
    font-style: normal;
  }
  
  @font-face {
    font-family: 'BebasNue';
    src: url(${bebasBold}) format('woff2');
    font-weight: bold;
    font-style: normal;
  }
  
  @font-face {
    font-family: 'rubik';
    src: url(${rubikBlack}) format('woff2');
    font-weight: normal;
    font-style: normal;
  }
`;

const container = document.getElementById("root");
const Root = () => (
  <Fragment>
    <GlobalStyle />
    <MuiThemeProvider theme={theme}>
      <App />
    </MuiThemeProvider>
  </Fragment>
);

const El = hot(module)(Root);

if (container != null) {
  ReactDOM.render(<El />, container);
}
