// @flow

import React from "react";
import SizedText from "../SizedText";

type Props = {
  text: string,
  style: ?any
};

function determineLinesNumber(text) {
  const { length } = text;

  if (length < 2) {
    return 1;
  }
  if (length < 6) {
    return 2;
  }
  if (length < 10) {
    return 3;
  }
  if (length < 14) {
    return 4;
  }
  return 5;
}

function getLinesLengths(textLength, linesNumber) {
  const lengths = new Array(linesNumber).fill(0);
  for (let i = 0; i < textLength; i++) {
    lengths[i % linesNumber] += 1;
  }
  return lengths;
}

function splitIntoLines(text) {
  const linesNumber = determineLinesNumber(text);
  const nameLength = text.length;
  const lines = new Array(linesNumber).fill("");
  const lengths = getLinesLengths(text.length, linesNumber);
  let lineIndex = 0;

  for (let i = 0; i < nameLength; i++) {
    if (lines[lineIndex].length >= lengths[lineIndex]) {
      lineIndex += 1;
    }

    lines[lineIndex] += text[i];
  }

  return lines;
}

export default ({ text, style }: Props) => {
  return (
    <svg
      viewBox="0 0 297 420"
      style={style}
      xmlns="http://www.w3.org/2000/svg"
      xmlnsXlink="http://www.w3.org/1999/xlink"
    >
      <SizedText
        x={150}
        y={100}
        width={297}
        strokeWidth={2}
        fill="none"
        stroke="#000"
      >
        {splitIntoLines(text).map((t, i) => (
          <tspan
            x={150}
            dy="0.3em"
            key={i}
            lengthAdjust="spacing"
            textAnchor="middle"
          >
            {t}
          </tspan>
        ))}
      </SizedText>
    </svg>
  );
};
