// @flow

import React from "react";

type Font = {
  classes: string[],
  symbolWidth: number,
  symbolHeight: number
};

type Props = {
  text: string,
  font: Font,
  style: ?any
};

const Text = ({ text, font, fz, offset }) => {
  return (
    <text
      x={0}
      y={70 + offset}
      fontFamily="CSTMXPRMNTL01"
      fontSize={fz}
      className={font.classes.join(" ")}
      // textLength={297}
      fill="#fff"
    >
      {text}
    </text>
  );
};

export default ({ text, font, style }: Props) => {
  let offset = 0;
  let fz = 100;
  const notes = text
    .toUpperCase()
    .split("-")
    .join("- ")
    .split(" ")
    .map(note => {
      const size = (297 / note.length) * font.symbolWidth;
      fz = size < fz ? size : fz;
      return note;
    })
    .map(note => {
      offset += fz * font.symbolHeight;
      return (
        <Text text={note} font={font} fz={fz} offset={offset} key={note} />
      );
    });

  return (
    <svg
      viewBox="0 0 297 420"
      style={style}
      xmlns="http://www.w3.org/2000/svg"
      xmlnsXlink="http://www.w3.org/1999/xlink"
    >
      {notes}
    </svg>
  );
};
