// @flow

import React, { PureComponent } from "react";

export default class extends PureComponent<*> {
  ref = React.createRef();

  render() {
    return (
      <tspan
        {...Object.assign({}, this.props, {
          fill: "none",
          stroke: "none",
          fontSize: this.props.width,
          ref: this.ref
        })}
      >
        {this.props.children}
      </tspan>
    );
  }

  calculate() {
    const { width, children, update } = this.props;
    const span: any = this.ref.current;
    const fieldWidth = Math.round(span.getBBox().width);

    update(children, width * (width / fieldWidth));
  }

  componentDidMount() {
    this.calculate();
  }

  componentDidUpdate() {
    this.calculate();
  }
}
