// @flow

import React from "react";

type Font = {
  classes: string[],
  symbolWidth: number,
  symbolHeight: number
};

type Props = {
  text: string,
  font: Font,
  fz: number,
  style: ?any
};

const Text = ({ text, font, fz, offset, rotate }) => {
  const width = fz * (font.symbolWidth * 0.58) * text.length;
  const height = fz * font.symbolHeight * 1.5;
  return (
    <>
      <rect
        fill="#00f"
        width={width}
        height={height}
        x={(297 - width) / 2}
        y={offset + height * 1.55}
        rx={8}
        ry={8}
        transform={`rotate(${rotate} ${(297 - width) / 2},${offset +
          height * 1.55})`}
      />
      <text
        x={148}
        y={70 + offset}
        fontFamily="CSTMXPRMNTL01"
        fontSize={fz}
        className={font.classes.join(" ")}
        textAnchor="middle"
        // textLength={297}
        fill="#fff"
        transform={`rotate(${rotate} ${(297 - width) / 2},${offset +
          height * 1.55})`}
      >
        {text}
      </text>
    </>
  );
};

export default ({ text, font, style, fz }: Props) => {
  let offset = 40;
  const rotate = Math.random() * 90 - 45;
  const notes = (text.length < 18
    ? [
        text.toUpperCase(),
        text.toUpperCase(),
        text.toUpperCase(),
        text.toUpperCase(),
        text.toUpperCase(),
        text.toUpperCase()
      ]
    : text
        .toUpperCase()
        .split("-")
        .join("- ")
        .split(" ")
  ).map((note, i) => {
    offset += fz * font.symbolHeight * 2;
    return (
      <Text
        text={note}
        font={font}
        fz={fz}
        offset={offset}
        rotate={rotate}
        key={i}
      />
    );
  });

  return (
    <svg
      viewBox="0 0 297 420"
      style={style}
      xmlns="http://www.w3.org/2000/svg"
      xmlnsXlink="http://www.w3.org/1999/xlink"
    >
      {notes}
    </svg>
  );
};
