// @flow

import * as React from "react";
import { Container, Print, Image } from "./styled";
import black from "./black.jpg";
import blue from "./blue.jpg";
import red from "./red.jpg";
import white from "./white.jpg";

type Props = {
  color: string,
  children?: React.Node
};

const getPicture = color => {
  switch (color) {
    case "black":
      return black;
    case "blue":
      return blue;
    case "red":
      return red;
    default:
      return white;
  }
};

export const TShirt = ({ color, children }: Props) => (
  <Container>
    <Image src={getPicture(color)} />
    <Print>{children}</Print>
  </Container>
);
