// @flow

import React, { Component } from "react";
import JFedor from "../prints/JFedor";
import { District } from "../../contexts";

export default class extends Component<*> {
  static contextType = District;

  ref = React.createRef();

  getPrint() {
    const { current } = this.ref;
    return current && typeof current.getPrint === "function"
      ? current.getPrint()
      : "";
  }

  render() {
    return (
      <JFedor
        style={{
          position: "absolute",
          top: 0,
          left: 0,
          display: "block",
          width: "100%",
          height: "100%"
          // background: "#f00"
        }}
        text={this.context.district}
        ref={this.ref}
      />
    );
  }
}
