// @flow

import React from "react";

type Font = {
  classes: string[],
  symbolWidth: number,
  symbolHeight: number
};

type Props = {
  text: string,
  font: Font,
  style: ?any
};

const Text = ({ text, font, fz, offset }) => {
  return (
    <text
      x={0}
      y={50 + offset}
      fontFamily="CSTMXPRMNTL01"
      fontSize={fz}
      className={font.classes.join(" ")}
      textLength={297}
      fill="#fff"
    >
      {text}
    </text>
  );
};

export default ({ text, font, style }: Props) => {
  let offset = 0;
  const notes = text
    .toUpperCase()
    .split("-")
    .join(" — ")
    .split(" ")
    .map(note => {
      let fz = (297 / note.length) * font.symbolWidth;

      if (note.length === 1) {
        fz /= 4;
      }

      offset += fz * font.symbolHeight;

      if (note.length === 1) {
        offset -= fz * 0.1;
      }

      return (
        <Text text={note} font={font} fz={fz} offset={offset} key={note} />
      );
    });

  return (
    <svg
      viewBox="0 0 297 420"
      style={style}
      xmlns="http://www.w3.org/2000/svg"
      xmlnsXlink="http://www.w3.org/1999/xlink"
    >
      {notes}
    </svg>
  );
};
